.add-employee-form {
  width: 80%;
  max-width: 800px;
  min-width: 250px;
  margin: auto;
  padding: 10px 30px;
}

.each-add-field {
  transition: all 0.3s ease-out;
  max-width: 100% !important;
  border-radius: 3px !important;
  border: 1px solid rgb(216, 216, 216);
  background: var(--white) !important;
  padding-left: 10px;
  height: 40px;
  margin: 5px 0;
}

.each-add-field:focus-within {
  border-color: rgb(137, 136, 136);
}

.add-employee-form-container {
  padding: 20px 0;
}

/* .phone-add-remove-container {
} */

.small-buttons {
  border-radius: 0;
  min-width: fit-content;
  margin: 0 !important;
  /* margin: 0 x; */
}

.small-button-loading {
  padding: 10px 20px;
}

.not-loading {
  padding: 6px 10px;
}

.right-border {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
