.each-employee-view {
  width: 100%;
}
.each-view-buttons-container {
  margin-top: 20px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
  grid-column-gap: 6px;
  grid-row-gap: 6px;
}
.each-view-buttons-container > button {
  margin: 5px 5px;
  min-width: fit-content;
}

.back-button {
  margin: 5px 0;
  padding: 0;
  min-width: fit-content;
}
.yes-button {
  border: 1px solid var(--blue);
  background-color: transparent;
  color: var(--blue);
  transition: all 0.3s ease;
}
.yes-button:hover {
  color: white;
  background-color: var(--blue);
}

.cancel-button {
  background-color: var(--blue);
}

.modal-buttons-container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, minmax(100px, 1fr));
  grid-column-gap: 6px;
  grid-row-gap: 6px;
}

.view-employee-form {
  width: 80%;
  min-width: 250px;
  max-width: 800px;
  margin: auto;
  padding: 10px 30px;
}

.view-employee-form-container {
  padding: 20px 0;
}
