/* Layout */
.s-layout {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 100vh;
  z-index: 1000;
}

.s-layout__content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1;
  margin-left: 4em;
}

/* Sidebar */

.s-layout_header {
  background: var(--nav-header-color);
  width: calc(100% - 4em);
  margin-left: 4em;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  box-shadow: 0 0 10px 0 var(--nav-box-shadow-color);
}
.s-layout_heading {
  position: relative;
  width: 100%;
  height: calc(4em + 1px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.s-sidebar__trigger {
  background: var(--nav-toggler-bg-color);
  position: absolute;
  top: 31px;
  left: 0;
  z-index: 1000;
  transform: translateY(-50%);
  width: 4em;
  /* border-top-right-radius: 10px;
  border-bottom-right-radius: 10px; */
  padding: 20px;
  /* padding-right: 10px; */
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.4s ease-in;
}
.open-animation {
  animation: open 0.3s forwards;
}

@keyframes open {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

/* .s-sidebar__nav .s-sidebar__trigger
,.s-sidebar__nav:focus .s-sidebar__trigger  
{
  left: 15em !important;
} */

.s-sidebar__trigger > i {
  display: inline-block;
  /* margin: 1.5em 0 0 1.5em; */
  color: var(--nav-toggler-color);
  font-size: 25px;
}

.s-sidebar__nav {
  /* position: relative;  */
  position: fixed;
  top: 0;
  left: -15em;
  z-index: 2;
  overflow-x: hidden;
  transition: all 0.4s ease-in;
  width: 15em;
  height: 100vh;
  background: var(--nav-background);
  color: var(--nav-text-color);
  box-shadow: 0 0 10px 0 var(--nav-box-shadow-color);
}

.s-sidebar__nav::-webkit-scrollbar {
  width: 6px;
}

.s-sidebar__nav::-webkit-scrollbar-thumb {
  background: rgba(248, 248, 248, 0.592);
  border-radius: 10px;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.s-sidebar__nav::-webkit-scrollbar-thumb:hover {
  background: grey;
}

.s-sidebar__nav .routes-container {
  position: absolute;
  top: 4em;
  left: 0;
  margin: 0;
  padding: 0;
  width: 15em;
}

.s-sidebar__nav ul li {
  width: 100%;
}

.s-sidebar__nav-link {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 55px;
  transition: all 0.4s ease;
  cursor: pointer;
}

.s-sidebar__nav-link em,
.s-layout-dropdown em {
  position: absolute;
  top: 50%;
  left: 4em;
  transform: translateY(-50%);
}
.s-layout-dropdown {
  cursor: pointer;
  position: relative !important;
  display: inline-block;
  width: 100% !important;
  max-height: 4em;
  overflow: hidden;
  transition: all 0.6s ease;
}
.s-layout-dropdown > .dropdown-heading {
  height: 4em;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  justify-content: space-between;
}
.dropdown-heading > i {
  margin-right: 2em;
}
.s-layout-dropdown > .dropdown-heading:hover {
  background: var(--nav-link-hover-background);
}
.s-layout-dropdown > .dropdown-heading:hover {
  color: var(--nav-link-hover-color);
}
/* .dropdown-heading:hover .s-layout-icon_container {
  color: var(--nav-link-hover-color) !important;
} */
.s-layout-dropdown > .dropdown-heading:hover .s-layout-icon_container > i {
  color: var(--nav-link-hover-color);
}
.s-layout-dropdown > .dropdown-heading:hover + ul {
  color: var(--nav-text-color) !important;
}
.s-layout-profile {
  width: 3em;
  height: 3em;
  border-radius: 50%;
  background-size: cover;
  /* background-attachment: fixed; */
  background-position: center;
  transition: all 0.4s ease;
  border: none;
  /* box-shadow: none; */
}
/* .s-layout-profile_container > a {
  visibility: hidden;
} */
.s-layout-profile_active > .s-layout-profile {
  box-shadow: 0 0 10px 0 var(--profile-active-color1),
    0 0 10px 0 var(--profile-active-color2) !important;
}

@keyframes drop {
  0% {
    max-height: 4em;
  }
  100% {
    max-height: 500px;
    overflow: visible;
  }
}

.s-layout-dropdown > ul {
  list-style: none;
  transition: all 0.4s ease;
}

.s-sidebar__nav-link:hover {
  background: var(--nav-link-hover-background);
}
.s-sidebar__nav-link:active {
  transform: scale(0.9);
}

.s-layout-active_link {
  color: var(--nav-link-active-color) !important;
  border-left: 2px solid var(--nav-link-active-color);
}

.s-layout-icon_container {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 4em;
  height: 100%;
}

.s-layout-icon_container i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-style: normal;
  font-weight: bolder;
  z-index: 2;
}

