/* Mobile First */
@media (min-width: 500px) {
  .s-sidebar__trigger {
    display: none;
  }
  .routes-container {
    top: 0 !important;
  }
  .page-content {
    margin-left: 0 !important;
  }
  .s-layout_header {
    width: calc(100% - 15em) !important;
    margin-left: 15em;
  }
}

.page-content {
  flex: 1 1;
  margin-top: 56px;
  transition: margin 0.4s ease-out;
  margin-left: 4em;
}
@media (min-width: 0) {
  .s-sidebar__nav {
    width: 4em;
    left: 0;
  }

  .s-sidebar__nav:hover {
    width: 15em;
  }
  /* .s-sidebar__nav:hover .s-layout .nav_open {
    position: relative !important;
  } */
  /* .nav_close .page-content {
    margin-left: 4em;
  } */
