  text-align: center;
  position: relative;
  cursor: pointer;
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  border: 0;
  transition: border-radius linear 0.05s, width linear 0.05s;
  transition: transform 0.3s ease;
}
.button:active {
  transform: scale(0.9);
}
.button:disabled {
  cursor: no-drop;
  opacity: 0.8;
}
.button:active {
  opacity: 0.5;
}
.button:focus {
  outline: 0;
}
.button.animate {
  width: 48px;
  height: 48px;
  min-width: 0;
  border-radius: 50%;
