.react-table-container h3 {
  display: inline-block;
  position: relative;
  text-align: center;
  font-size: 1.5em;
  color: #cecece;
}
.react-table-container h3:before {
  content: "\25C0";
  position: absolute;
  left: -50px;
  animation: leftRight 2s linear infinite;
}
.react-table-container h3:after {
  content: "\25b6";
  position: absolute;
  right: -50px;
  animation: leftRight 2s linear infinite reverse;
}
@keyframes leftRight {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-10px);
  }
  75% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0);
  }
}

