/* :hover 4d84e2 */
.panels-container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.signin-container:before {
  content: "";
  position: absolute;
  height: 2000px;
  width: 2000px;
  top: -10%;
  right: 48%;
  transform: translateY(-50%);
  background-image: linear-gradient(-45deg, #4481eb 0%, #04befe 100%);
  transition: 1.8s ease-in-out;
  border-radius: 50%;
  z-index: 6;
}

.image {
  width: 100%;
  transition: transform 1.1s ease-in-out;
  transition-delay: 0.4s;
}

.panel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align: center;
  z-index: 6;
}

.left-panel {
  pointer-events: all;
  padding: 3rem 17% 2rem 12%;
}

.right-panel {
  pointer-events: none;
  padding: 3rem 12% 2rem 17%;
}

.panel .content {
  color: #fff;
  transition: transform 0.9s ease-in-out;
  transition-delay: 0.6s;
}

.panel h3 {
  font-weight: 600;
  line-height: 1;
  font-size: 1.5rem;
}

.panel p {
  font-size: 0.95rem;
  padding: 0.7rem 0;
}

.btn.transparent {
  margin: 0;
  background: none;
  border: 2px solid #fff;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem;
}

.right-panel .image,
.right-panel .content {
  transform: translateX(800px);
}

/* ANIMATION */

.signin-container.sign-up-mode:before {
  transform: translate(100%, -50%);
  right: 52%;
}

.signin-container.sign-up-mode .left-panel .image,
.signin-container.sign-up-mode .left-panel .content {
  transform: translateX(-800px);
}

.signin-container.sign-up-mode .signin-signup {
  left: 25%;
}

.signin-container.sign-up-mode form.sign-up-form {
  opacity: 1;
  z-index: 2;
}

.signin-container.sign-up-mode form.sign-in-form {
  opacity: 0;
  z-index: 1;
}

.signin-container.sign-up-mode .right-panel .image,
.signin-container.sign-up-mode .right-panel .content {
  transform: translateX(0%);
}

.signin-container.sign-up-mode .left-panel {
  pointer-events: none;
}

.signin-container.sign-up-mode .right-panel {
  pointer-events: all;
}

@media (max-width: 870px) {
  .signin-container {
    /* min-height: 800px; */
    height: 100vh;
  }
  .signin-signup {
    width: 100%;
    top: 85%;
    transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
  }

  .signin-signup,
  .signin-container.sign-up-mode .signin-signup {
    left: 50%;
  }

  .panels-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }

  .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
    grid-column: 1 / 2;
  }

  .right-panel {
    grid-row: 3 / 4;
  }

  .left-panel {
    grid-row: 1 / 2;
  }

  .image {
    width: 200px;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.6s;
  }

  .panel .content {
    padding-right: 15%;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.8s;
  }

  .panel h3 {
    font-size: 1.2rem;
  }

  .panel p {
    font-size: 0.7rem;
    padding: 0.5rem 0;
  }

  .signin-container:before {
    width: 1500px;
    height: 1500px;
    transform: translateX(-50%);
    left: 30%;
    bottom: 68%;
    right: auto;
    right: initial;
    top: auto;
    top: initial;
    transition: 2s ease-in-out;
  }

  .signin-container.sign-up-mode:before {
    transform: translate(-50%, 100%);
    bottom: 32%;
    right: auto;
    right: initial;
  }

  .signin-container.sign-up-mode .left-panel .image,
  .signin-container.sign-up-mode .left-panel .content {
    transform: translateY(-300px);
  }

  .signin-container.sign-up-mode .right-panel .image,
  .signin-container.sign-up-mode .right-panel .content {
    transform: translateY(0px);
  }

  .right-panel .image,
  .right-panel .content {
    transform: translateY(300px);
  }

  .signin-container.sign-up-mode .signin-signup {
    top: 5%;
    transform: translate(-50%, 0);
  }
}

@media (max-width: 570px) {
  .sign-in-form {
    padding: 0 1.5rem;
  }

  .image {
    display: none;
  }
  .panel .content {
    padding: 0.5rem 1rem;
  }
  .signin-container {
    padding: 1.5rem;
  }

  .signin-container:before {
    bottom: 72%;
    left: 50%;
  }

  .signin-container.sign-up-mode:before {
    bottom: 28%;
    left: 50%;
  }
}

